<template>
  <div class="about">

    <v-row>
      <v-dialog
          v-model="newdialog"
          persistent
          max-width="600px"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
              elevation="5"
              :style="{position: 'fixed', width: '64px', height: '64px', bottom: '64px', right: '140px'}"
              fab
              dark
              absolute
              color="primary"
              v-bind="attrs"
              v-on="on"
          >
            <v-icon dark>
              mdi-plus
            </v-icon>
          </v-btn>
        </template>
        <v-card>
          <v-card-title>
            <span class="headline">Lisa uus liige</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col
                    cols="12"
                    sm="6"
                >
                  <v-text-field
                      label="Eesnimi*"
                      required
                      v-model="member.firstName"
                  ></v-text-field>
                </v-col>
                <v-col
                    cols="12"
                    sm="6"
                >
                  <v-text-field
                      label="Perekonnanimi*"
                      required
                      v-model="member.lastName"

                  ></v-text-field>
                </v-col>
                <v-col cols="12"
                       sm="6">
                  <v-text-field
                      label="Email*"
                      required
                      v-model="member.email"
                  ></v-text-field>
                </v-col>
                <v-col cols="12"
                       sm="6">
                  <v-text-field
                      label="Phone*"
                      required
                      v-model="member.phone"
                  ></v-text-field>
                </v-col>
                <v-col
                    cols="12"
                    sm="6"
                >
                  <v-select
                      :items="['6. ky', '5. ky', '4. ky', '3. ky',
                        '2. ky', '1. ky', '1. dan', '2. dan', '3. dan',
                        '4. dan', '5. dan', '6. dan', '7. dan', '8. dan', '9. dan']"
                      label="Level*"
                      required
                      v-model="member.level"
                  ></v-select>
                </v-col>

                <v-col cols="12"
                       sm="6">
                  <template>
                    <v-menu
                        ref="menu"
                        v-model="menu"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        min-width="290px"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                            v-model="member.birthDate"
                            label="Birthday date"
                            prepend-icon="mdi-calendar"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                          ref="picker"
                          v-model="member.birthDate"
                          :max="new Date().toISOString().substr(0, 10)"
                          min="1950-01-01"
                          @change="save"
                      ></v-date-picker>
                    </v-menu>
                  </template>
                </v-col>

              </v-row>
            </v-container>
            <small>*Nutud vli</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
                color="red"
                text
                @click="newdialog = false"
            >
              Sulge
            </v-btn>
            <v-btn
                color="green"
                text
                @click="addNewMember"
            >
              Salvesta
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>

    <member-element v-for="(item, index) in resultList" :key="item.memberID"
                    :index="index+1"
                    :firstName="item.firstName"
                    :lastName="item.lastName"
                    :level="item.level"
                    :phone="item.phone"
                    :email="item.email"
                    :birthDate="item.birthDate"
                    :memberID="item.memberID"
                    @delete-member="deleteMember"
                    @edit-member="editMember"
    >
    </member-element>

  </div>
</template>

<script>

import MemberElement from "@/components/MemberElement";

export default {
  components: {MemberElement},
  methods: {
    getData() {
      this.$http.get(this.$host + "/intraweb/allmembers")
          .then(response => this.resultList = response.data);
    },
    addNewMember() {
      this.newdialog = false;
      // console.log(this.member)
      this.$http.post(this.$host + "/intraweb/newmember", this.member)
      this.getData();
      this.member.firstName = "";
      this.member.lastName = "";
      this.member.email = "";
      this.member.phone = "";
      this.member.level = "";
      this.member.birthDate = "";
    },
    save(date) {
      this.$refs.menu.save(date)
    },
    deleteMember(index) {
      let memberID = this.resultList[index - 1].memberID
      this.$http.delete(this.$host + `/intraweb/deletemember/${memberID}`);
      this.resultList.splice((index - 1), 1);
    },
    editMember(index, editedMember) {
      // console.log(editedMember)
      // console.log(index)
      //this.resultList[index-1] = editedMember;
      this.$http.patch(this.$host + `/intraweb/update/${editedMember.memberID}`, editedMember);
      this.getData();
    }
  },
  data() {
    return {
      member: {
        "firstName": "",
        "lastName": "",
        "email": "",
        "phone": "",
        "level": "",
        "birthDate": null
      },
      singleExpand: false,
      resultList: [],
      newdialog: false,
      editdialog: false,
      date: null,
      menu: false
    }
  },
  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = 'YEAR'))
    },
  },
  created() { //toimub enne DOMi loomist
    this.getData();
  }
}

</script>

