<template>

  <v-card
      elevation="0"
      class="mt-4 ml-0 mr-0 mb-4"
  >
    <v-card-title>
      <p class="display-1 text--primary pa-0 ma-0">{{ index }}. {{ firstName }} {{ lastName }}</p>
    </v-card-title>
    <v-card-text>
      <v-icon medium>mdi-account-box-outline</v-icon>
      {{ memberID }}
      <v-icon medium>mdi-chevron-triple-up</v-icon>
      {{ level }}
      <v-icon medium>mdi-mail</v-icon>
      {{ email }}
      <v-icon medium>mdi-phone</v-icon>
      {{ phone }}
      <v-icon medium>mdi-calendar-range</v-icon>
      {{ birthDate }}
    </v-card-text>
    <v-card-actions>
      <v-btn
          icon
          color="pink"
          @click="deleteMember"
      >
        <v-icon>mdi-delete</v-icon>
      </v-btn >


      <v-row>
        <v-dialog
            v-model="newdialog"
            persistent
            max-width="600px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn class="ml-4"
                icon
                color="primary"
                v-bind="attrs"
                v-on="on"
            >
              <v-icon>mdi-account-edit</v-icon>
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">Muuda</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                      cols="12"
                      sm="6"
                  >
                    <v-text-field
                        label="Eesnimi*"
                        required
                        :placeholder="firstName"
                        v-model="editedMember.firstName"
                    ></v-text-field>
                  </v-col>
                  <v-col
                      cols="12"
                      sm="6"
                  >
                    <v-text-field
                        label="Perekonnanimi*"
                        required
                        :placeholder="lastName"
                        v-model="editedMember.lastName"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12"
                         sm="6">
                    <v-text-field
                        label="Email*"
                        required
                        :placeholder="email"
                        v-model="editedMember.email"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12"
                         sm="6">
                    <v-text-field
                        label="Phone*"
                        required
                        :placeholder="phone"
                        v-model="editedMember.phone"
                    ></v-text-field>
                  </v-col>
                  <v-col
                      cols="12"
                      sm="6"
                  >
                    <v-select
                        :items="['6. ky', '5. ky', '4. ky', '3. ky',
                        '2. ky', '1. ky', '1. dan', '2. dan', '3. dan',
                        '4. dan', '5. dan', '6. dan', '7. dan', '8. dan', '9. dan']"
                        label="Level*"
                        required
                        :placeholder="level"
                        v-model="editedMember.level"
                    ></v-select>
                  </v-col>

                  <v-col cols="12"
                         sm="6">
                    <template>
                      <v-menu
                          ref="menu"
                          v-model="menu"
                          :close-on-content-click="false"
                          transition="scale-transition"
                          offset-y
                          min-width="290px"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                              v-model="editedMember.birthDate"
                              label="Birthday date"
                              prepend-icon="mdi-calendar"
                              readonly
                              v-bind="attrs"
                              v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker
                            v-model="editedMember.birthDate"
                            ref="picker"
                            :max="new Date().toISOString().substr(0, 10)"
                            min="1950-01-01"
                            @change="save"
                        ></v-date-picker>
                      </v-menu>
                    </template>
                  </v-col>

                </v-row>
              </v-container>
              <small>*Nutud vli</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                  color="red"
                  text
                  @click="newdialog = false"
              >
                Sulge
              </v-btn>
              <v-btn
                  color="green"
                  text
                  @click="editMember"
              >
                Salvesta
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-row>


    </v-card-actions>
  </v-card>

</template>

<script>
export default {
  data() {
    return {
      editedMember: {
        "memberID": null,
        "firstName": null,
        "lastName": null,
        "email": null,
        "phone": null,
        "level": null,
        "birthDate": null
      },
      singleExpand: false,
      newdialog: false,
      editdialog: false,
      date: null,
      menu: false
    }
  },
  props: {
    index: {},
    memberID: {
      type: Number,
      required: true,
    },
    firstName: {
      default: "No value",
    },
    lastName: {
      default: "No value",
    },
    birthDate: {
      default: "No value",
    },
    level: {
      default: "No value",
    },
    email: {
      default: "No value",
    },
    phone: {
      default: "No value",
    },
  },
  methods: {
    deleteMember() {
      this.$emit('delete-member', this.index);
    },
    editMember() {
      this.newdialog = false;
      this.$emit('edit-member', this.index, this.editedMember);

    },
    save (date) {
      this.$refs.menu.save(date)

    }
  },
  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = 'YEAR'))
    },
  },
  created() {
    this.editedMember.firstName = this.firstName
    this.editedMember.lastName = this.lastName
    this.editedMember.level = this.level
    this.editedMember.phone = this.phone
    this.editedMember.email = this.email
    this.editedMember.birthDate = this.birthDate
    this.editedMember.memberID = this.memberID
  }
}
</script>